.bannerHome {
  position: relative;
  margin: 15px 10px 15px 10px;
  width: 96%;
  height: 223px;
  border-radius: 10px;
}
.bannerBg {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background: black;
  opacity: 0.3;
  border-radius: 10px;
}
.bannerImg {
  height: 223px;
  background: #f7f7f7;
  border-radius: 10px;
  object-fit: cover;
  width: 100%;
}
.banner-text {
  text-align: center;
  font-size: 48px;
  line-height: 68px;
  width: 90%;
  padding: 0;
  margin-right: 10%;
  padding-left: 45px;
  color: #f7f7f7;
  position: absolute;
  top: 10%;
}
.bannerAbout {
  margin: 0 100px;
  width: 85%;
  max-width: 1240px;
  height: 223px;
  position: relative;
}
.aboutImg {
  border-radius: 10px;
  min-width: 336px;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (max-width: 740px) {
  .bannerHome {
    margin: 1%;
    text-align: center;
    height: 111px;
    width: 98%;
  }

  .bannerImg {
    height: 111px;
  }
  .banner-text {
    line-height: 24px;
    max-width: 95%;
    width: 70%;
    font-size: 24px;
  }
  .bannerAbout {
    width: 92%;
    margin: 20px;
  }
}

.card {
  width: 340px;
  height: 340px;
  margin: 10px;
  padding-top: 20px;
  width: 100%;
  position: relative;
}
.cardImg {
  height: 340px;
  object-fit: cover;
  border-radius: 10px;
  background: #ff6060;
  width: 335px;
}
.cardText {
  position: absolute;
  left: 5.88%;
  right: 5.88%;
  top: 88.73%;
  bottom: 5.88%;
  font-size: 18px;
  line-height: 142.6%;
  display: flex;
  align-items: flex-end;
  font-weight: 500;
  color: #ffffff;
}
@media (max-width: 740px) {
  .card {
    height: 255px;
  }
  .cardImg {
    height: 255px;
  }
}

.listCard {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  background-color: #f7f7f7;
  border-radius: 25px;
  position: relative;
  margin-left: 10px;
  margin-right: 10px;
}
@media (max-width: 740px) {
  .listCard {
    background-color: unset;
    justify-content: center;
    padding: 10px;
  }
}

.homeContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header {
  margin: 40px 100px 63px 100px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 68px;
}
header a img {
  /* width: 17%; */
  height: 68px;
}
header nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
header nav a {
  font-size: 24px;
  color: #ff6060;
}
#Accueil {
  margin-right: 30px;
}
#about {
  margin-left: 30px;
}

header nav a:hover,
header nav a:active {
  line-height: 34px;
  border-bottom: 1px solid #ff6060;
}
@media (max-width: 740px) {
  .header {
    margin: 20px 20px 12px 20px;
  }
  header a img {
    width: 100%;
    max-width: 145px;
    min-height: 47px;
  }
  header nav a {
    margin-right: 10px;
    font-size: 12px;
  }
  #Accueil {
    margin-right: 5px;
  }
  #about {
    margin-left: 5px;
  }
}

.kasa-dropdown {
  width: 100%;
  max-width: 1023px;
  height: auto;
  border-radius: 10px;
  text-align: left;
  margin-top: 31px;
}

.large {
  width: auto;
}

.dropdown-header {
  display: inline-flex;
  width: 100%;
  height: 25%;
  min-height: 50px;
  align-items: center;
  background-color: #ff6060;
  border-radius: 10px;
}

.dropdown-title {
  color: #ffffff;
  height: 50px;
  line-height: 50px;
  margin: 5px 10px;
}

.dropdown-arrow {
  display: flex;
  justify-content: flex-end;
  margin-right: 10px;
  width: 100%;
}

.dropdown-arrow.true {
  justify-content: start;
  -webkit-transform: rotate(-180deg);
          transform: rotate(-180deg);
}

.dropdown-arrow img {
  width: 25px;
}

.dropdown-description {
  height: auto;
  min-height: 146px;
  margin-top: -10px;
  padding: 25px 10px 10px 10px;
  background-color: #f6f6f6;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  color: #ff6060;
}
#dropdown-Description {
  margin-right: 38px;
}
#dropdown-Équipements {
  margin-left: 38px;
}
@media (max-width: 740px) {
  .dropdown-arrow img {
    width: 15px;
  }
  .kasa-dropdown {
    margin-top: 20px;
    width: 100%;
    min-width: 335px;
  }
  .dropdown-header {
    height: 30px;
  }
  .dropdown-title {
    font-size: 13px;
  }
  #dropdown-Équipements {
    margin-left: 0px;
  }
}

.about-dropdown {
