.kasa-wheel {
  width: 100%;
  height: 415px;
  margin: 0 100px;
  position: relative;
}

.picture-number {
  font-family: 'Montserrat';
  font-weight: 500;
  width: 24px;
  height: 32px;
  position: absolute;
  bottom: 20px;
  left: 50%;

  color: #ffffff;
}
.kasaArrow {
  display: flex;
}

.arrow {
  width: 46px;
  height: 79px;
  position: absolute;
}

.arrow:hover {
  cursor: pointer;
  -webkit-filter: drop-shadow(2px 4px 6px black);
          filter: drop-shadow(2px 4px 6px black);
}

.arrow-left {
  margin-left: 25px;
  top: 50%;
}

.arrow-right {
  margin-right: 25px;
  right: 0;
  top: 50%;
}

.wheel-image {
  display: none;
  width: 100%;
  height: 415px;
  border-radius: 25px;
  margin: 20px 0px;
}

.wheel-image.displayed {
  display: block;
  object-fit: cover;
}

@media (max-width: 740px) {
  .kasa-wheel {
    height: 335px;
    margin: 0;
  }
  .arrow {
    width: 12px;
    height: 19px;
  }
  .wheel-image {
    height: 335px;
    width: 100%;
    min-width: 255px;
    margin: 0;
  }
}

.kasa-tags {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.tag {
  padding: 0 15px;
  line-height: 170%;
  margin: 5px;
  font-size: 10px;
  background-color: #ff6060;
  color: white;
  border-radius: 5px;
}
@media (max-width: 740px) {
  .kasa-tags {
    margin-top: 30px;
    margin-left: 20px;
  }
  .tag {
    font-size: 14px;
    border-radius: 5px;
    margin: 0;
    margin-right: 10px;
  }
}

.host {
  display: flex;
  flex-direction: row;
  align-items: center;

  line-height: 142.6%;
}
.hostName {
  width: 80px;
  font-size: 18px;
  color: #ff6060;
  margin: 2px 10px 0 0;
  text-align: right;
  font-weight: 500;
}
.profilPictureHost {
  width: 64px;
  height: 64px;
  border-radius: 50px;
}

@media (max-width: 740px) {
