  }
}

.lodgingRating {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.lodgingRating img {
  -webkit-transform: scale(0.7);
          transform: scale(0.7);
  width: 30px;
  height: 30px;
}
@media (max-width: 740px) {
  .lodgingRating img {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    width: 15px;
    height: 15px;
  }
}

.lodging-header {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
h1,
h2 {
  font-family: Montserrat;
  color: #ff6060;
  font-weight: 500;
}
.hr {
  display: flex;
  flex-direction: column;
  height: 118px;
  margin-right: 100px;
}

.dropdownLodging {
  display: flex;
  flex-direction: row;
  margin: 24px 100px;
}
.header-dropdownLodging {
